import {BaseRequest,sendRequest} from "../../networking/request/oeRequest";
import objectParse from '../../util/objectParse';

export const PersonType={
  PersonTypeInCharge:0,//整个项目的责任人> 审核类型的成员
  PersonTypeExecutor:1,//流程执行人
  PersonTypeReviewer:2,//流程验收审核人
  PersonTypeManager:3,//选择管理员
}

/**
 * 获取不同人员列表
 * @param departmentId 部门id
 * @param oType   人员类型（项目责任人、流程执行人、流程审核人）
 * @param callback 回调方法
 */
export function getPersonListOfType(departmentId,oType,callback) {

  let params={
    oType:oType,
    type:2,
  };

  if (departmentId&&departmentId>0){
    params.departmentId=departmentId;
  }

  const baseRequest = new BaseRequest('getChargeList', params);

  baseRequest.success = function (baseResponse) {

    let  responsibleList=null;

    //责任人
    if (baseResponse.msgData && baseResponse.msgData instanceof Array){

      responsibleList=objectParse.objectArrayFormKeyValuesArray(baseResponse.msgData,function () {

        return new Responsible();
      });
    }

    if (callback){

      callback(true,responsibleList,baseResponse);
    }

  };

  baseRequest.fail = function (res) {

    if (callback){

      callback(false,null,res);
    }
  };

  sendRequest(baseRequest);

}

function Responsible() {

  this.userId=0;

  this.userName='';

  this.headUrl='';

  this.phoneno=null;

  this.departmentName='';

  this.position=''

  this.selected=false;

  this.ignoredKeys=function () {

    return ['selected'];
  }
}



